const sorted = data.sort(function (a, b) {
  return -a.time.localeCompare(b.time)
})

export const useJournal = () => useContext(JournalContext);

export const JournalProvider = ({ children }) => {

  // journal 'state' and hook
  const [journalEntries, setJournalEntries] = useState(sorted);

  // add a new journal entry
  const addJournalEntry = (newEntry) => {
    setJournalEntries((prevEntries) => {

      const newData = [...prevEntries, newEntry];

      return newData.sort(function (a, b) {
        return -a.time.localeCompare(b.time)
      })

    });
  };

  // edit an existing journal entry
  const editJournalEntry = (newEntry) => {

    setJournalEntries((prevEntries) => {

      const index = prevEntries.findIndex(entry => entry.id == newEntry.id);

      if (index !== -1) {
        prevEntries[index] = newEntry;
      }

      return prevEntries
    })
  }


  // delete an existing journal entry
  const deleteJournalEntry = (id) => {

    setJournalEntries((prevEntries) => {

      // must be immutable in prod
      return prevEntries.filter(entry => entry.id != id);

    })

  }

  return (
    <JournalContext.Provider value={{ journalEntries, addJournalEntry, editJournalEntry, deleteJournalEntry }}>
      {children}
    </JournalContext.Provider>
  );
};
